}

@media only screen and (max-width: 575px) {
  .hide-on-mob {
    display: none;
  }
}

.hide-on-desctop {
  display: inline-block;
}

@media only screen and (min-width: 575px) {
  .hide-on-desctop {
    display: none;
  }
}

.js-show {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

.js-fadeIn {
  opacity: 0;
  transition: opacity 1s ease;
}

.js-slideRight {
  opacity: 0;
  transform: translate(50px, 0);
}

.js-slideLeft {
  opacity: 0;
  transform: translate(-50px, 0);
}

.js-show-onload {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

.svg-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  fill: #ffffff;
  transition: fill 0.3s;
}

@media only screen and (min-width: 1024px), (any-hover: hover) {
  .svg-icon:hover {
    fill: #C71413;
  }
}

.header {
  position: absolute;
  background-color: transparent;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  padding: 20px 0;
}

.header-row {
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-family: "AmesRoman", cursive, sans-serif;
  font-weight: 400;
  font-size: 40px;
  line-height: 0.8em;
  margin: 0;
}

.logo span {
  position: relative;
  font-weight: 400;
}

.logo span:first-child {
  color: #e9e9e9;
  -webkit-text-stroke: 1px #e9e9e9;
  -webkit-text-fill-color: #C71413;
  display: inline-block;
  z-index: 10;
}

.logo span:last-child {
  margin-left: -.62em;
  color: #e9e9e9;
  -webkit-text-stroke: 1px #e9e9e9;
  -webkit-text-fill-color: #e9e9e9;
}

.humburger {
  background-color: transparent;
  width: 30px;
  height: 16px;
  position: relative;
  border: none;
  z-index: 10;
}

@media only screen and (max-width: 991px) {
  .humburger {
    display: inline-block;
  }
}

.humburger.open .humburger-item:first-child, .humburger.open .humburger-item:last-child {
  width: 0;
  transition-property: width;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(2) {
  transform: rotate(45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(3) {
  transform: rotate(-45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item {
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
}

.humburger-item:first-child {
  top: 0;
  left: 0;
  width: 75%;
  transition-property: width;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(2) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(3) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:last-child {
  bottom: 0;
  right: 0;
  width: 75%;
  transition-property: width;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.section-home {
  background-color: #1d1e1f;
